'use client'

import { motion } from 'framer-motion'
import { Upload, Cpu, Eye } from 'lucide-react'

const steps = [
  {
    icon: Upload,
    title: 'Upload & Capture',
    description: 'Import your existing photos, videos, and notes or capture new memories directly through the app.',
    color: 'blue',
  },
  {
    icon: Cpu,
    title: 'AI Organization',
    description: 'Our AI analyzes content, recognizes faces, locations, and events to automatically organize everything.',
    color: 'purple',
  },
  {
    icon: Eye,
    title: 'Rediscover & Share',
    description: 'Easily search, revisit, and share your organized memories with intelligent recommendations.',
    color: 'green',
  },
]

export default function HowItWorks() {
  return (
    <section id="how-it-works" className="py-20 bg-gradient-to-br from-gray-50 to-blue-50">
      <div className="container mx-auto px-6">
        <motion.div
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
          viewport={{ once: true }}
          className="text-center mb-16"
        >
          <h2 className="text-4xl lg:text-5xl font-bold text-gray-900 mb-6">
            How <span className="gradient-text">Memorae AI</span> Works
          </h2>
          <p className="text-xl text-gray-600 max-w-3xl mx-auto">
            Three simple steps to transform how you preserve and relive your memories
          </p>
        </motion.div>

        <div className="grid lg:grid-cols-3 gap-8 max-w-6xl mx-auto">
          {steps.map((step, index) => (
            <motion.div
              key={step.title}
              initial={{ opacity: 0, y: 30 }}
              whileInView={{ opacity: 1, y: 0 }}
              transition={{ duration: 0.5, delay: index * 0.2 }}
              viewport={{ once: true }}
              className="relative"
            >
              {/* Step Number */}
              <div className="absolute -top-4 -left-4 w-12 h-12 bg-white rounded-full shadow-lg flex items-center justify-center text-xl font-bold text-gray-900 border">
                {index + 1}
              </div>

              <div className="bg-white rounded-2xl p-8 shadow-lg hover-lift h-full border border-gray-100">
                <div className={`inline-flex p-4 rounded-xl bg-${step.color}-100 text-${step.color}-600 mb-6`}>
                  <step.icon className="h-8 w-8" />
                </div>
                
                <h3 className="text-2xl font-semibold text-gray-900 mb-4">
                  {step.title}
                </h3>
                
                <p className="text-gray-600 leading-relaxed">
                  {step.description}
                </p>
              </div>
            </motion.div>
          ))}
        </div>

        {/* Animated connecting line for desktop */}
        <div className="hidden lg:flex justify-between items-center max-w-4xl mx-auto mt-8 px-16">
          {steps.map((_, index) => (
            <motion.div
              key={index}
              initial={{ scale: 0 }}
              whileInView={{ scale: 1 }}
              transition={{ delay: index * 0.3 + 0.5 }}
              viewport={{ once: true }}
              className="w-4 h-4 bg-blue-500 rounded-full"
            />
          ))}
          <div className="absolute left-0 right-0 h-1 bg-blue-200 -z-10 mx-16" />
        </div>
      </div>
    </section>
  )
}